<template>
    <div id="roles">
        <!--  -->
        <el-breadcrumb separator="/" style="padding-left:10px;padding-bottom:10px;font-size:12px;">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input
                            clearable
                            size="small"
                            placeholder=""
                            v-model="queryMap.roleName"
                            class="input-with-select"
                            @clear="getRoleList"
                    >
                        <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="2">
                    <el-button
                            size="small"
                            v-hasPermission="'role:add'"
                            type="success"
                            icon="el-icon-circle-plus-outline"
                            @click="addDialogVisible=true"
                    ></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button
                            size="small"
                            v-hasPermission="'role:export'"
                            icon="el-icon-download"
                            @click="downExcel"
                    ></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <template>
                <el-table
                        v-loading="loading"
                        :data="roleData"
                        border
                        style="width: 100%;margin-top:20px;"
                        height="470"
                        size="small"
                >
                    <el-table-column prop="id" label="ID" width="180"></el-table-column>
                    <el-table-column prop="roleName" label="" width="180"></el-table-column>
                    <el-table-column prop="createTime" label="" width="150"></el-table-column>
                    <el-table-column prop="isban" label="" width="100">
                        <template slot-scope="scope">
                            <el-switch v-model="scope.row.status" @change="changRoleStatus(scope.row)"></el-switch>
                        </template>
                    </el-table-column>
                    <el-table-column prop="remark" label=""></el-table-column>
                    <el-table-column fixed="right" label="" width="200">
                        <template slot-scope="scope">
                            <el-button

                                    @click="grant(scope.row.id)"
                                    type="text"
                                    icon="el-icon-present"
                                    size="small"
                            ></el-button>
                            <el-button @click="edit(scope.row.id)" v-hasPermission="'role:edit'" type="text" icon="el-icon-edit" size="small"></el-button>
                            <el-button
                                    v-hasPermission="'role:delete'"
                                    @click="del(scope.row.id)"
                                    type="text"
                                    icon="el-icon-delete"
                                    size="small"
                            ></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
            <!--  -->
            <el-pagination
                    background
                    style="margin-top:10px;"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryMap.pageNum"
                    :page-sizes="[8, 16, 32, 64]"
                    :page-size="queryMap.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
            ></el-pagination>
            <!--  -->
            <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="closeAdd">
                <el-form ref="addFormRef" :model="addForm" label-width="80px" :rules="addFormRoles">
                    <el-form-item label="" prop="roleName">
                        <el-input v-model="addForm.roleName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="remark">
                        <el-input type="textarea" v-model="addForm.remark"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button
                  type="primary"
                  @click="addRole"
                  :loading="btnLoading"
                  :disabled="btnDisabled"
          > </el-button>
        </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="closeEdit">
                <el-form ref="editFormRef" :model="editForm" label-width="80px" :rules="addFormRoles">
                    <el-form-item label="" prop="roleName">
                        <el-input v-model="editForm.roleName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="remark">
                        <el-input type="textarea" v-model="editForm.remark"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button
                  type="primary"
                  @click="updateRole"
                  :loading="btnLoading"
                  :disabled="btnDisabled"
          > </el-button>
        </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="grantDialogVisible" width="38%">
        <span>
          <el-tree
                  :auto-expand-parent="false"
                  :data="data"
                  show-checkbox
                  node-key="id"
                  :default-expanded-keys="open"
                  :props="defaultProps"
                  ref="tree"
                  highlight-current
          ></el-tree>
            <!-- check-strictly -->
        </span>
                <span slot="footer" class="dialog-footer">
          <el-button @click="grantDialogVisible = false"> </el-button>
          <el-button
                  v-hasPermission="'role:authority'"
                  type="primary"
                  icon="el-icon-setting"
                  @click="authority"
                  :loading="btnLoading"
                  :disabled="btnDisabled"
          > </el-button>
        </span>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        data() {
            return {
                btnLoading: false,
                btnDisabled: false,
                loading: true,
                total: 0,
                queryMap: { roleName: "", pageNum: 1, pageSize: 8 }, //
                roleData: [], //
                addForm: {}, //
                editForm: {}, //
                addDialogVisible: false, //
                editDialogVisible: false, //
                grantDialogVisible: false, //
                data: [],
                open: [], //
                grantId: "",
                defaultProps: {
                    children: "children",
                    label: "menuName"
                },
                addFormRoles: {
                    roleName: [
                        { required: true, message: "", trigger: "blur" },
                        { min: 3, max: 10, message: " 3  10 ", trigger: "blur" }
                    ],
                    remark: [
                        { required: true, message: "", trigger: "blur" },
                        { min: 5, max: 20, message: " 5  20 ", trigger: "blur" }
                    ]
                } //
            };
        },
        methods: {

            /**
             * 
             */
            downExcel() {
                const $this = this;
                const res = axios.request({
                    url: "system/role/excel",
                    method: "post",
                    responseType: "blob"
                })
                    .then(res => {
                        if(res.headers['content-type']==='application/json'){
                            return $this.$message.error("Subject does not have permission [role:export]");
                        }
                        const data = res.data;
                        let url = window.URL.createObjectURL(data); // url
                        const a = document.createElement("a");
                        document.body.appendChild(a);
                        a.href = url;
                        a.download = ".xls";
                        a.click();
                        window.URL.revokeObjectURL(url);
                    });
            },

            //
            async authority() {
                this.btnDisabled = true;
                this.btnLoading = true;
                const { data: res } = await this.$http.post(
                    "system/role/authority/" + this.grantId,
                    [].concat(
                        this.$refs.tree.getCheckedKeys(),
                        this.$refs.tree.getHalfCheckedKeys()
                    )
                );
                if (res.success) {
                    this.$message.success("");
                } else {
                    this.$message.error(":" + res.data.errorMsg);
                }
                this.btnDisabled = false;
                this.btnLoading = false;
                this.grantDialogVisible = false;
            },

            //
            async grant(id) {
                //id
                const { data: res } = await this.$http.get("system/role/findRoleMenu/" + id);
                if (res.success) {
                    //
                    let that = this;
                    setTimeout(function() {
                        res.data.mids.forEach(value => {
                            that.$refs.tree.setChecked(value, true, false);
                        });
                    }, 100);
                    this.data = res.data.tree;
                    this.open = res.data.open;
                    this.grantId = id; //id
                }
                this.grantDialogVisible = true;
            },
            //
            async getRoleList() {
                const { data: res } = await this.$http.get("system/role/findRoleList", {
                    params: this.queryMap
                });
                if (res.success) {
                    this.roleData = res.data.rows;
                    this.total = res.data.total;
                }
            },
            //
            search() {
                this.queryMap.pageNum = 1;
                this.getRoleList();
            },
            //
            closeAdd() {
                this.$refs.addFormRef.clearValidate();
                this.addForm = {};
            },
            closeEdit() {
                this.$refs.editFormRef.clearValidate();
                this.editForm = {};
            },
            //
            async addRole() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) {
                        return;
                    } else {
                        this.btnDisabled = true;
                        this.btnLoading = true;
                        const { data: res } = await this.$http.post("system/role/add", this.addForm);
                        if (res.success) {
                            this.$message.success("");
                            this.addDialogVisible = false;
                            this.btnDisabled = false;
                            this.btnLoading = false;
                            this.addForm = {};
                            await this.getRoleList();
                        } else {
                            return this.$message.error(":" + res.data.errorMsg);
                        }
                    }
                });
            },
            //
            async edit(id) {
                const { data: res } = await this.$http.get("system/role/edit/" + id);
                if (res.success) {
                    this.editForm = res.data;
                    this.editDialogVisible = true;
                } else {
                    return this.$message.error(":" + res.data.errorMsg);
                }
            },
            //
            async updateRole() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) {
                        return;
                    } else {
                        this.btnDisabled = true;
                        this.btnLoading = true;
                        const { data: res } = await this.$http.put(
                            "system/role/update/" + this.editForm.id,
                            this.editForm
                        );
                        if (res.success) {
                            this.$notify({
                                title: "",
                                message: "",
                                type: "success"
                            });
                            await this.getRoleList();
                        } else {
                            this.$message.error(":" + res.data.errorMsg);
                        }

                        this.editDialogVisible = false;
                        this.btnDisabled = false;
                        this.btnLoading = false;
                        this.editForm = {};

                    }
                });
            },
            //
            async del(id) {
                var res = await this.$confirm(
                    ", ?",
                    "",
                    {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    }
                ).catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
                if (res === "confirm") {
                    const { data: res } = await this.$http.delete("system/role/delete/" + id);
                    console.log(res);
                    if (res.success) {
                        this.$message.success("");
                        await this.getRoleList();
                    } else {
                        this.$message.error(":" + res.data.errorMsg);
                    }
                }
            },
            //
            async changRoleStatus(row) {
                const { data: res } = await this.$http.put(
                    "system/role/updateStatus/" + row.id + "/" + row.status
                );
                if (!res.success) {
                    this.$message.error(":" + res.data.errorMsg);
                    row.status = !row.status;
                } else {
                    this.$message.success("");
                }
            },
            //
            handleSizeChange(newSize) {
                this.queryMap.pageSize = newSize;
                this.getRoleList();
            },
            //
            handleCurrentChange(current) {
                this.queryMap.pageNum = current;
                this.getRoleList();
            }
        },
        created() {
            this.getRoleList();
            setTimeout(() => {
                this.loading = false;
            }, 500);
        }
    };
</script>
